.label_form {
  display: grid;
  grid-template-columns: 1fr 3fr 6fr;
}

.results-box {
  margin-left: 10px;
}

@media screen and (max-width: 803px) {
  .label_form {
    display: flex;
    flex-direction: column;
  }
  .results-box {
    margin-left: 0px;
    /* width: 50% */
  }
}